import React, { Component } from 'react';
import "../statics/styles.scss"
import { connect } from "react-redux"
import {Form, Button, Col} from 'react-bootstrap'


class Personal_information extends Component {
    render() {
        return (
            <main className="m-2" style={{paddingBottom: '3rem'}}>
                <div className="mt-4 mb-4">
                    <h2>עדכון פרטים אישיים</h2>
                </div>
                <Form>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formBasicEmail">
                            <Form.Label>שם פרטי</Form.Label>
                            <Form.Control readOnly value={this.props.userData.firstName} />
                        </Form.Group>
                        <Form.Group as={Col} controlId="formBasicEmail">
                            <Form.Label>שם משפחה</Form.Label>
                            <Form.Control readOnly value={this.props.userData.lastName} />
                        </Form.Group>
                        <Form.Group as={Col} controlId="formBasicEmail">
                            <Form.Label>תעודת זהות</Form.Label>
                            <Form.Control readOnly value={this.props.userData.tz} />
                        </Form.Group>
                    </Form.Row>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formBasicEmail">
                            <Form.Label>כתובת דואר אלקטרוני</Form.Label>
                            <Form.Control type="email" value={this.props.userData.email}/>
                        </Form.Group>
                    </Form.Row>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formBasicPassword">
                            <Form.Label>רחוב ומספר</Form.Label>
                            <Form.Control />
                        </Form.Group>
                        <Form.Group as={Col} controlId="formBasicPassword">
                            <Form.Label>עיר מגורים</Form.Label>
                            <Form.Control />
                        </Form.Group>
                    </Form.Row>
                    <div className="mt-4 mb-4">
                    <h2>שינוי סיסמה</h2>
                    </div>
                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>סיסמה חדשה</Form.Label>
                        <Form.Control type="password" />
                        <Form.Text className="text-muted">
                        הקלד פעם נוספת
                        </Form.Text>
                    </Form.Group>
                    
                    <Button variant="primary" type="submit">
                        שמירת שינויים
                    </Button>
                </Form>
            </main>
        );
    }
}
export default connect(state => state)(Personal_information);